ables, por lo general entre el 1 % y el 10 % anual. Este tipo de inflación se considera manejable e incluso normal en una economía en crecimiento. De hecho, muchos bancos centrales tienen como meta una inflación controlada de entre el 2 % y el 4 %, puesto que una ligera inflación incentiva el consumo y la inversión. Por ejemplo, si una economía presenta una inflación anual del 3 %, los consumidores pueden preferir gastar en lugar de ahorrar, estimulando así la actividad económica. La inflación moderada permite ajustes de precios relativos sin generar distorsiones graves.")])]),_c('div',{staticClass:"div",attrs:{"titulo":"Inflación galopante"}},[_c('p',[_vm._v("Ocurre cuando los precios aumentan a tasas de dos o tres dígitos anuales, es decir, entre el 20 % y el 100 % o más. Este tipo de inflación comienza a generar serias dificultades económicas, como pérdida de poder adquisitivo, incertidumbre en la inversión, distorsión en los precios y problemas en la planificación financiera. Un ejemplo histórico se dio en Argentina durante la década de 1980, cuando la inflación galopante superó el 200 % anual, generando desconfianza en la moneda local y empobreciendo rápidamente a la clase media. Este tipo de inflación obliga a las personas a cambiar sus patrones de consumo y ahorro, muchas veces refugiándose en monedas extranjeras o bienes duraderos.")])]),_c('div',{staticClass:"div",attrs:{"titulo":"Hiperinflación"}},[_c('p',[_vm._v("La forma más extrema del fenómeno inflacionario es la "),_c('b',[_vm._v("Hiperinflación")]),_vm._v(", que se presenta cuando los precios suben de manera descontrolada, superando el 1.000 % anual. Este tipo de inflación destruye completamente el valor del dinero, provocando una crisis económica, social y política. En contextos de hiperinflación, los precios pueden duplicarse cada poco día, lo que imposibilita la planificación financiera incluso a corto plazo. Uno de los casos más conocidos es el de Venezuela en los últimos años, donde el Fondo Monetario Internacional reportó tasas anuales de inflación superiores al 1.000.000 % en 2018. La gente recurría al trueque o al uso de monedas extranjeras como el dólar para realizar sus transacciones cotidianas.")])])])],1)]),_vm._m(7),_vm._m(8),_vm._m(9),_c('p',{staticClass:"mb-4"},[_vm._v("La inflación no solo afecta el valor del dinero, sino que también genera una serie de consecuencias económicas y sociales que impactan el bienestar general de la población.")]),_c('div',{staticClass:"bg-full-width bg-color-2 mb-5"},[_c('div',{staticClass:"px-4 p-md-5"},[_c('div',{staticClass:"row justify-content-center align-items-center"},[_c('div',{staticClass:"col-lg-7 mb-3 mb-lg-0"},[_c('h2',{staticClass:"mb-4",attrs:{"data-aos":"flip-up"}},[_vm._v("Consecuencias de la inflación sobre la economía y la población ")]),_vm._m(10),_c('a',{staticClass:"anexo mb-4 bg-white w-fit",attrs:{"href":_vm.obtenerLink('/downloads/Anexo_1.pdf'),"target":"_blank","data-aos":"flip-up"}},[_c('div',{staticClass:"anexo__icono",style:({'background-color': '#FCDFDB'})},[_c('img',{attrs:{"src":require("@/assets/template/icono-pdf.svg")}})]),_vm._m(11)])]),_vm._m(12)])])]),_c('div',{staticClass:"bg-full-width border-top color-primario"},[_c('div',{staticClass:"p-4 p-md-5"},[_c('h2',{attrs:{"data-aos":"fade-left"}},[_vm._v("MATERIAL COMPLEMENTARIO")]),_c('div',{staticClass:"row material-complementario"},[_c('div',{staticClass:"col-12 col-md-6 col-lg-7"},[_c('p',[_vm._v("Los invitamos a explorar el material complementario de este curso, en esta sección encontrará recursos que le permitirán profundizar y enriquecer su aprendizaje en los temas tratados en esta unidad.")]),_c('p',{staticClass:"d-flex my-4"},[_c('img',{staticClass:"me-3",style:({'max-width':'16px'}),attrs:{"src":require("@/assets/componentes/link.svg")}}),_c('a',{attrs:{"href":"https://es.wikipedia.org/wiki/Inflaci%C3%B3n ","target":"_blank","rel":"noopener noreferrer"}},[_vm._v("colaboradores de Wikipedia. (2025a, marzo 12). Inflación - Wi